@use '../util/' as * ;

.toggle {
    display: grid;
    grid-template-columns: 1fr rem(60) 1fr;
    grid-row: repeat (2, 1fr);
    border: none;
    gap: rem(4.8) rem(8);
    margin: 0;

    label {
        font-size: rem(14);
        align-self: center;

        &[for='dark'] {
            grid-column: 1 / 2;
            justify-self: end;
        }
    }

    label {
        &[for='system'] {
            grid-column: 1 / 4;
            grid-row: 2 / 3;
            justify-self: center;
            align-self: start;
        }
    }

    label {
        &[for='light'] {
            grid-column: 3 / 4;
            justify-self: center;
        }
    }
    
    &__wrapper {
        position: relative;
        grid-column: 2 / 3;
        grid-row: 1 / 2;
        height: rem(24);
    }

    input[type="radio"]{
        margin: 0 -2px 0 -2px;
        opacity: 0;
        width: rem(20);
        height: rem(20);

        &:focus ~ .toggle__button {
            border: 2px solid white;
        }
    }

    &__background{
        display: block;
        height: 100%;
        position: absolute;
        width: 100%;
        top: 0;
        border-radius: rem(12);
        overflow: hidden;
        background: linear-gradient(225deg, 
        var(--toggle-bg-end) 0%,
        var(--toggle-bg-start) 98.02%);
        pointer-events: none;
    }

    &__button {
        position: absolute;
        left: rem(3);
        top: rem(3);
        right: 100%;
        border-radius: 50%;
        width: rem(18);
        height: rem(18);
        background-color: var(--dark-bg);
        transition: all 150ms ease-in-out;
    }

    #light:checked ~ .toggle__button {
        left: calc(100% - 21px);
        right: 3px;
    }

    #system:checked ~ .toggle__button {
        left: calc(50%);
        right: auto;
        transform: translate(-50%);
    }
}